document.addEventListener("DOMContentLoaded", () => {
    const addRequisites = document.querySelector(".add-requisites");

    const createContractTemplateForm = () => {
        return `
            <div class="requisites-form">
                <input class="new-item__input requisites-form__name" type="text" name="name-project2" placeholder="     ">
                    <div class="requisites-form__column">
                        <span class="requisites-form__column-desc"> ()</span>
                        <select class="select requisites-form__input" name="person">
                            <option value="physical"> . </option>
                            <option value="legal">. </option>
                        </select>
                        <input class="new-item__input requisites-form__input" type="text" name="full name" placeholder="">
                        <input class="new-item__input requisites-form__input" type="text" name="Full name in genitive" placeholder="   ">
                        <input class="new-item__input requisites-form__input" type="text" name="inn" placeholder="">
                        <textarea class="new-item__input requisites-form__input requisites-form__textarea" type="text" name="place of residence" placeholder=" "></textarea>
                        <input class="new-item__input requisites-form__input" type="text" name="passport, series number" placeholder=",  ">
                        <input class="new-item__input requisites-form__input" type="tel" name="phone" placeholder="">
                        <input class="new-item__input requisites-form__input" type="email" name="Email" placeholder="Email">
                    </div>
                    <div class="requisites-form__column">
                        <span class="requisites-form__column-desc"></span>
                        <select class="select requisites-form__input" name="person">
                            <option value="physical"> . </option>
                            <option value="legal">. </option>
                        </select>
                        <input class="new-item__input requisites-form__input" type="text" name="full name" placeholder="">
                        <input class="new-item__input requisites-form__input" type="text" name="Full name in genitive" placeholder="   ">
                        <input class="new-item__input requisites-form__input" type="text" name="inn" placeholder="">
                        <textarea class="new-item__input requisites-form__input requisites-form__textarea" type="text" name="place of residence" placeholder=" "></textarea>
                        <input class="new-item__input requisites-form__input" type="text" name="passport, series number" placeholder=",  ">
                        <input class="new-item__input requisites-form__input" type="tel" name="phone" placeholder="">
                        <input class="new-item__input requisites-form__input" type="email" name="Email" placeholder="Email">
                    </div>
                <p class="requisites-form__desc new-item__desc">   ,     . <br><br> ,  ,                   (      ).</p>
            </div>
        
        `
    }
    
    if(addRequisites) {
        let flag = 0;
        addRequisites.addEventListener("click", (e) => {
            let newItem = e.currentTarget.closest(".new-item");
            let newItemContent = newItem.querySelector(".new-item__content");
            if(flag == 0) {
                e.currentTarget.innerHTML = "";
                newItem.classList.add("is-active");
                newItemContent.insertAdjacentHTML("beforeend", createContractTemplateForm());
                flag++;
            } else {
                let requisitesForm = document.querySelector(".requisites-form")
                e.currentTarget.innerHTML = "";
                newItem.classList.remove("is-active");
                requisitesForm.remove();
                flag--;
            }
        });
    }
});