document.addEventListener("DOMContentLoaded", () => {
    const newItemAuthor = document.querySelectorAll(".new-item__author");
    const newItemMembers = document.querySelector(".new-item__members");
    
    const removeChoicesImages = (choicesItem) => {
        let images = choicesItem.querySelectorAll("img");
        images.forEach(el => el.remove());
    }
    
    if(newItemAuthor && newItemMembers) {
        newItemMembers.addEventListener("change", (e) => {
            const specialists = document.querySelector('[value="members"]');
            const btnClear = document.querySelector(".btn-clear");
            const createItemsAuthors = () => {
                return `
                    <div class="new-item__authors">
                        <button class="btn-reset author new-item__author" type="button" data-name=" " data-src="img/stage-1.png"><img class="author__image" src="img/stage-1.png" alt=" "><span class="author__name"> </span></button>
                        <button class="btn-reset author new-item__author" type="button" data-name=" " data-src="img/stage-2.png"><img class="author__image" src="img/stage-2.png" alt=" "><span class="author__name"> </span></button>
                        <button class="btn-reset author new-item__author" type="button" data-name=" " data-src="img/stage-3.png"><img class="author__image" src="img/stage-3.png" alt=" "><span class="author__name"> </span></button>
                        <button class="btn-reset author new-item__author" type="button" data-name=" " data-src="img/stage-4.png"><img class="author__image" src="img/stage-4.png" alt=" "><span class="author__name"> </span></button>
                        <button class="btn-reset author new-item__author" type="button" data-name=" " data-src="img/stage-5.png"><img class="author__image" src="img/stage-5.png" alt=" "><span class="author__name"> </span></button>
                    </div>
                `
            }
            const createImgAuthor = (src, name) => {
                return `
                    <img class="author__image author__image--small" src="${src}" alt="${name}">
                `
            }
            if(specialists) {
                btnClear.insertAdjacentHTML('beforebegin', createItemsAuthors());
                const newItemAuthors = document.querySelector(".new-item__authors");
                const newItemAuthor = document.querySelectorAll(".new-item__author");
                newItemAuthor.forEach(el => {
                    el.addEventListener("click", (e) => {
                        el.classList.toggle("new-item__author--active")
                        let choicesItem = newItemMembers.querySelector(".choices__item--selectable");
                        let dataSrc = el.dataset.src;
                        let dataName = el.dataset.name;
                        if(el.classList.contains("new-item__author--active")) {
                            choicesItem.innerHTML += createImgAuthor(dataSrc, dataName)
                        } else {
                            choicesItem.querySelector(`[src="${dataSrc}"]`).remove()
                        }
                    })
                });
            } else {
                const newItemAuthors = document.querySelector(".new-item__authors");
                const newItemAuthor = document.querySelectorAll(".new-item__author");
                newItemAuthor.forEach(el => el.classList.remove("new-item__author--active"));
                if(newItemAuthors) {
                    newItemAuthors.remove();
                }
            }
            btnClear.addEventListener("click", (e) => {
                const newItemAuthors = document.querySelector(".new-item__authors");
                const newItemAuthor = document.querySelectorAll(".new-item__author");
                let choicesItem = newItemMembers.querySelector(".choices__item--selectable");
                newItemAuthor.forEach(el => el.classList.remove("new-item__author--active"));
                removeChoicesImages(choicesItem);
            })
        });
    }
});