import { initializeSelect } from "../../components/select";

document.addEventListener("DOMContentLoaded", () => {

    /*  */

    const createProjectBlock = () => {
        return `
            <div class="new-project__data-block">
                <select class="select">
                    <option value=""> </option>
                    <option value="bathroom"></option>
                    <option value="brick"></option>
                    <option value="children"></option>
                    <option value="brick"></option>
                    <option value="corridor"></option>
                    <option value="kitchen"></option>
                    <option value="bedroom"></option>
                    <option value="canteen"></option>
                    <option value="wardrobe"></option>
                    <option value="another">...</option>
                </select>
                <button class="btn-reset btn-remove remove-data" type="button"></button>
                <input class="new-item__input new-project__data-input" type="text" name="area" placeholder=", ">
            </div>
        `
    }

    const createProjectData = (element) => {
        element.insertAdjacentHTML("beforeend", createProjectBlock());
    }

    /*  */

    document.addEventListener("click", (e) => {
        if(e.target.classList.contains("create-data")) {
            const newProjectDataLeft = e.target.closest(".new-project__data-left");
            createProjectData(newProjectDataLeft);
        }

        if(e.target.classList.contains("remove-data")) {
            e.target.closest(".new-project__data-block").remove();
        }

        const selects = document.querySelectorAll('.select');

        selects.forEach(el => {
            if(el.classList.contains("select") && !el.classList.contains("choices__input")) {
                initializeSelect(el);
            }
        })
    });
});